import React, { Component } from 'react';
import { BrowserRouter as Router } from "react-router-dom";
import Nav from 'components/Nav';
import Routes from './routes';

export default class App extends Component {
  render() {
    return (
      <Router>
        <div>
          <Nav />
          <Routes />
        </div>
      </Router>
    );
  }
}



